.flex {
  display: flex;
  flex-wrap: wrap;
}
.flex > * {
  flex-grow: 1;
}
input,
select,
textarea,
input.form-control,
select.form-control,
textarea.form-control {
  border-radius: 4px;
  border: 2px solid #d3d3d3;
  color: #211915;
}
input:focus,
select:focus,
textarea:focus,
input.form-control:focus,
select.form-control:focus,
textarea.form-control:focus {
  border: 2px solid #ffa500;
}
input[disabled],
select[disabled],
textarea[disabled],
input.form-control[disabled],
select.form-control[disabled],
textarea.form-control[disabled] {
  border: 2px solid #a9a9a9;
}
input.is-error,
select.is-error,
textarea.is-error {
  border: 2px solid #de4040;
  color: #211915;
}
input.is-valid,
select.is-valid,
textarea.is-valid {
  border: 2px solid #84c140;
  color: #211915;
}
input[type="file"] {
  border: 0;
}
select,
select.form-control {
/* styling */
  display: inline-block;
  line-height: 1.5em;
  padding: 0.5em 3.5em 0.5em 1em;
/* reset */
  margin: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
}
select,
select.form-control {
  background-image: url("http://www.pngmart.com/files/3/Down-Arrow-PNG-Clipart.png");
  background-position: calc(100% - 20px) 1em;
  background-size: 15px;
  background-repeat: no-repeat;
}
.btn {
  border-radius: 3px;
  padding: 10px 40px;
  color: #fff;
  background-color: #20388a;
  transition: 'carmem' 1s, box-shadow 1s;
}
