body {
  height: 100vh;
  background-color: #f5f5f5;
  overflow: hidden;
}
header {
  padding: 10px 0 0 0;
  text-align: center;
}
.logo {
  max-width: 80px;
  height: auto;
}
a,
a:hover {
  color: #fff;
  text-decoration: none;
}
.chamanento {
  text-align: center;
}
.chamanento img {
  margin-bottom: 20px;
}
.manifiesto-desc {
  padding: 10px;
}
.manifiesto-desc h5 {
  padding-top: 20px;
  padding-right: 10px;
  padding-left: 10px;
  border-top: 1px solid #d3d3d3;
}
.logobiz {
  position: absolute;
  bottom: 10px;
  width: 100%;
}
.logobiz img {
  max-width: 40px;
  margin-left: auto;
  margin-right: auto;
}
.flex-wrapper {
  display: flex;
  width: 100%;
  height: 100%;
}
.flex-wrapper > * {
  width: 100%;
  align-self: center;
}
.tecladBtn {
  max-width: 700px;
  padding: 20px;
}
.logoMetanoia-bottom {
  position: absolute;
  top: 10px;
  width: 100%;
  text-align: center;
}
.logoMetanoia-bottom img {
  max-width: 120px;
  max-width: auto;
}
.w-btns {
  max-width: 467px;
  margin-top: 40px;
  display: flex;
  background-color: #353333;
  color: #fff;
  border-radius: 5px;
  justify-content: center;
  flex-wrap: wrap;
  box-shadow: 2px 4px 2px #a9a9a9;
  padding: 20px;
  margin: auto;
  padding-top: 40px;
  padding-bottom: 40px;
  border: 2px solid #000;
  border-left: 5px solid #262626;
  border-bottom: 5px solid #000;
  border-top: 1px solid #232222;
}
.w-btns .btn-chamamento {
  align-self: center;
  width: 32%;
  height: 80px;
  text-align: center;
  padding: 5px;
  margin-top: 10px;
}
.w-btns .btn-chamamento .clicavel {
  border: 1px solid #000;
  border-bottom: 3px solid #000;
  cursor: pointer;
  background-color: #272424;
  border-radius: 5px;
}
.w-btns .btn-chamamento .clicavel:active {
  border-bottom: 1px solid #000;
  box-shadow: inset 4px 4px 10px rgba(0,0,0,0.8), 0 0 10px rgba(0,0,0,0);
  background-color: #232222;
}
.w-btns .btn-chamamento .clicavel h4 {
  margin: 0;
  padding: 0;
}
.btns-main {
  border: 1px solid #000;
  border-bottom: 3px solid #000;
  cursor: pointer;
  background-color: #272424;
  border-radius: 2px;
  color: #fff;
  padding: 10px 30px;
  font-size: 20px;
}
.confirmar {
  border: 1px solid #098c3d;
  background-color: #0baf4c;
  border-bottom: 3px solid #055826;
}
.confirmar:active {
  border-bottom: 1px solid #000;
  box-shadow: inset 4px 4px 4px rgba(0,0,0,0.3), 0 0 10px rgba(0,0,0,0);
  background-color: #098c3d;
}
[disabled] {
  border: 1px solid #666;
  background-color: #808080;
  border-bottom: 3px solid #404040;
}
.corrigir {
  border: 1px solid #bb490a;
  background-color: #ea5b0c;
  border-bottom: 3px solid #752d06;
}
.corrigir:active {
  border-bottom: 1px solid #000;
  box-shadow: inset 4px 4px 4px rgba(0,0,0,0.3), 0 0 10px rgba(0,0,0,0);
  background-color: #bb490a;
}
.teclado {
  max-width: 700px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 1px 1px 2px #bfbebe;
}
.teclado button {
  width: 48%;
}
.manifesto {
  margin: auto;
  border-radius: 100%;
}
.logobiz-bottom {
  position: absolute;
  bottom: 10px;
  width: 100%;
  text-align: center;
}
.logobiz-bottom img {
  margin: auto;
  max-width: 30px;
}
.w-visor {
  position: relative;
  height: 660px;
  max-width: 620px;
  margin: auto;
  border: 4px solid #fff;
  text-align: center;
  border-left: 4px solid #e6e6e6;
  border-bottom: 2px solid #f5eeee;
  border-radius: 10px;
  box-shadow: inset 2px 2px 2px rgba(0,0,0,0.2), 0 0 10px rgba(0,0,0,0);
  background-color: #fffff9;
  margin-top: 0;
}
.w-visor h1 {
  font-size: 200px;
}
.ranking-flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.ranking-flex > * {
  width: 24%;
  padding: 5px 4px;
}
.ranking-flex .well {
  height: 100%;
  min-height: 20px;
  padding: 10px;
  margin-bottom: 20px;
  background-color: #fff;
  border: 1px solid #e3e3e3;
}
.bg-chama {
  background: url("../img/chama.svg") center center no-repeat;
  background-size: 200px;
  background-position: bottom left;
}
.ranking h1 {
  font-size: 7rem;
  color: #ed863a;
}
.ranking .frase {
  color: #000;
}
.ranking .frase h5 {
  text-transform: capitalize;
}
.ranking .frase h5 .position {
  color: #ed863a;
  font-size: 35px;
}
.carinha {
  position: absolute;
  top: 10px;
  right: 10px;
}
.carinha img {
  max-width: 10px;
}
@media screen and (max-height: 800px) {
  .ranking h1 {
    font-size: 7rem;
    color: #ed863a;
  }
  .ranking .frase {
    color: #000;
  }
  .ranking .frase h5 {
    font-size: 20px;
    text-transform: capitalize;
  }
}
@supports (-ms-ime-align: auto) {
  .selector {
    color: #f00;
  }
}
